import { Component } from './component.js';
import { render } from './render';
import { registerTypes } from './register.js';
import { DomAppManager } from './dom.manager';

export function launch({ types, template = '<App/>',  ...props } = {}) {
  DomAppManager.template = template;
  registerTypes([DomAppManager].concat(types))
  const a = new Component(DomAppManager, { props });

  const fn = () => { render(a); a.init(); }
  if (typeof window === 'object') {
    window.requestAnimationFrame(fn);
  } else {
    fn()
  }

  return a.impl;
}

if (typeof window === 'object') {
  window.launch = launch;
}